:root {
  --background-color: hsl(207, 26%, 17%);
  --background-color-accent: hsl(209, 23%, 22%);
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "nunito", sans-serif;
}

body {
  width: 100vw;
  height: 100vh;
}

.loading-screen {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--background-color);
}

.loading {
  font-size: 40px;
  color: white;
}

.App {
  min-width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--background-color);
}

.body-head {
  width: 100%;
}

.search-bar {
  align-self: flex-start;
  justify-self: start;
  padding-left: 20px;
}

.header {
  height: 80px;
  width: 100%;
  z-index: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 40px;
  background-color: var(--background-color-accent);
  color: var(--white);
}

.icon-moon {
  width: 25px;
  height: 25px;
  cursor: pointer;
  justify-items: end;
}

.moon-text-container {
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 10px;
}

.detail-container {
  background-color: var(--background-color);
  color: var(--white);
  display: flex;
  flex-direction: column;
  padding: 50px 10px;
  font-weight: 600;
}

.detail-container > button {
  width: 104px;
  height: 32px;
  background-color: var(--background-color-accent);
  color: var(--white);
  border-style: none;
  box-shadow: 0 0 7px 0 hsla(0, 0%, 0%, 0.267);
  margin-bottom: 60px;
  border-radius: 5px;
}

.detail-container img {
  object-fit: cover;
  max-width: 300px;
  border-radius: 10px;
}

.detail-container h3 {
  margin-top: 50px;
}

.detail-container button:hover {
  cursor: pointer;
  box-shadow: 0 0 12px 0 hsla(0, 0%, 0%, 0.267);
}

.detail-container button:active {
  box-shadow: 0 0 8px 0 hsla(0, 0%, 0%, 0.267);
}

.detail-container > p {
  margin-top: 50px;
}

.detail-sub-container {
  align-items: center;
  justify-content: center;
}

.detail-container-1 {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.detail-container-2 {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 50px;
}

.detail-container-3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  margin-top: 10px;
  justify-content: center;
}

.detail-container-3 > button {
  height: 38px;
  background-color: var(--background-color-accent);
  box-shadow: 0 2px 9px 0 hsla(0, 0%, 0%, 0.05);
  color: var(--white);
  border-style: none;
  cursor: pointer;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.info-container > p {
  margin-top: 40px;
}

li {
  list-style: none;
}

.body {
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

h1 {
  font-size: 14px;
  font-weight: 800;
}

input {
  background-color: var(--background-color-accent);
  box-shadow: 0 0px 10px 4px hsla(0, 0%, 0%, 0.05);
  color: var(--white);
  border-style: none;
  width: 100%;
  height: 48px;
  margin-top: 20px;
  border-radius: 5px;
}

input:hover {
  box-shadow: 0 0 7px 0 hsla(0, 0%, 0%, 0.281);
}

::placeholder {
  color: var(--white);
}

#placeholder::placeholder {
  color: black;
}

.filter-list {
  align-self: flex-start;
  position: relative;
}

.filter-container {
  display: flex;
  justify-content: space-evenly;
  gap: 0px;
  align-items: center;
  width: 200px;
  height: 48px;
  margin-top: 40px;
  border-radius: 5px;
  border-style: none;
  background-color: var(--background-color-accent);
  box-shadow: 0 0px 9px 2px hsla(0, 0%, 0%, 0.05);
  color: var(--white);
}

.filter-container:hover {
  cursor: pointer;
  box-shadow: 0 0 7px 0 hsla(0, 0%, 0%, 0.281);
}

.list {
  background-color: var(--background-color-accent);
  margin-top: 4px;
  border-radius: 5px;
  color: var(--white);
  padding: 15px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  position: absolute;
  width: 200px;
}

.icon {
  width: 30px;
  height: 20px;
  stroke: var(--white);
}

.card {
  width: 264px;
  height: 336px;
  margin-top: 40px;
  background-color: var(--background-color-accent);
  box-shadow: 0 0 8px 5px hsla(0, 0%, 0%, 0.05);
  display: flex;
  flex-direction: column;
  border-radius: 5px;
}

.card:hover {
  cursor: pointer;
  box-shadow: 0 0 14px 0 hsla(0, 0%, 0%, 0.281);
}

.card:active {
  box-shadow: 0 0 9px 0 hsla(0, 0%, 0%, 0.281);
}

.card img {
  height: 50%;
  border-radius: 5px 5px 0 0;
  object-fit: cover;
}

.card-body {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 5px;
  padding: 20px;
  color: var(--white);
  font-weight: 600;
}

h3 {
  margin-bottom: 10px;
}

span {
  font-weight: 300;
}

.regions {
  cursor: pointer;
}

.regions:hover {
  font-weight: bold;
}

.off {
  display: none;
}

@media (min-width: 800px) {
  .detail-container img {
    max-width: 440px;
  }

  .detail-container > button {
    margin-bottom: 10px;
  }

  .detail-container-2 {
    margin-top: 0;
  }

  .detail-container-3 {
    justify-self: center;
    margin-top: 10px;
  }

  .detail-sub-container {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 6%;
  }

  .info-container {
    display: flex;
    flex-direction: column;
  }

  .info-container > p {
    margin-top: 40px;
  }

  .info-container h3 {
    font-size: 32px;
  }

  .info-sub-container {
    display: flex;
    gap: 50px;
  }
}

@media (max-width: 450px) {
  .detail-container-3 {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 600px) {
  .body-body {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 40px;
  }

  .body-head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
  }

  .filter-container {
    margin: 0;
  }

  input {
    margin: 0;
  }

  input {
    width: 40%;
  }
}

@media (min-width: 800px) {
  .header {
    font-size: 24px;
  }

  h1 {
    font-size: 24px;
  }

  .detail-container-3 {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 900px) {
  .body-body {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 40px;
  }

  .detail-container-3 {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 1200px) {
  .body-body {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 40px;
  }

  .body-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 85%;
  }
}
